// Create.vue

<template>
  <div class="container">
        <div class="card">
            <div class="card-header">
                <h3>Add Subscriber</h3>
            </div>
            <div class="card-body">
                <form v-on:submit.prevent="addSubscriber">
                    <div class="form-group">
                        <label>Email:</label>
                        <input type="text" class="form-control" v-model="subscriber.email"/>
                    </div>
                    <div class="form-group">
                        <label>Name:</label>
                        <input type="text" class="form-control" v-model="subscriber.name"/>
                    </div>
                    <div class="form-group">
                        <label>State:</label>
                        <select id="state" class="form-control" v-model="subscriber.state">
                            <option value="active">Active</option>
                            <option value="unsubscribed">Unsubscribed</option>
                            <option value="junk">Junk</option>
                            <option value="bounce">Bounce</option>
                            <option value="unconfirmed">Unconfirmed</option>
                        </select>
                    </div>
                    <div class="form-group">
                        <button type="button" class="btn btn-secondary"  v-on:click="addField()">New Field</button>
                    </div>
                    <div class="form-group">
                            <div v-for="(line, index) in lines" :key="index" class="row">
                            <div class="col-lg-10">
                                <div class="row">
                                    <div class="col-3">
                                        <input class="form-control" v-model="line.title" placeholder="Title" />
                                    </div>
                                    <div class="col-3">
                                        <select class="form-control" v-model="line.type">
                                            <option value="date">Date</option>
                                            <option value="number">Number</option>
                                            <option value="string">String</option>
                                            <option value="boolean">Boolean</option>
                                        </select>
                                    </div>
                                    <div class="col-5">
                                        <input class="form-control" v-model="line.value" label="Value" placeholder="Value" />
                                    </div>
                                </div>
                            </div>

                            <div class="col-2">
                                <div class="block float-right">
                                <button type="button" round v-on:click="removeField(index)"><i class="fa fa-close"></i></button>
                                <button type="button" round v-if="index + 1 === lines.length" v-on:click="addField()"><i class="fa fa-plus"></i></button>
                                </div>
                            </div>
                            </div>
                    </div>
                    <div class="form-group">
                        <input type="submit" class="btn btn-primary" value="Add Item"/>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    components: {
        //name: 'AddItem'
    },
    data() {
        return {
            subscriber: {},
            lines: [],
            blockRemoval: true
        }
    },
    methods: {
        addSubscriber() {
            this.subscriber.fields = [];
            this.lines.forEach((value) => {
                if (value.type == null) {
                    this.subscriber.fields = [];
                    alert("You must select a type for the field.");
                    return;
                }
                this.subscriber.fields.push(value);
            });
            let uri = 'http://localhost/api/subscriber/create.php';
            this.axios.post(uri, this.subscriber).then((response) => {
                console.log(response.data);
            }).catch(err => {
                alert(err.response.data.message);
            });
        },
        addField() {
            let checkEmptyLines = this.lines.filter(line => line.title === null)
            if (checkEmptyLines.length >= 1 && this.lines.length > 0) {
                alert("You must complete this fied before adding a new one.")
                return
            }
            this.lines.push({
                title: null,
                type: null,
                value: null
            })
        },
        removeField(lineId) {
            this.lines.splice(lineId, 1);
        }
    }
}
</script>