// Index.vue

<template>
    <div>
        <h1>Subscribers</h1>

        <table class="table table-hover">
            <thead>
            <tr>
                <td>ID</td>
                <td>Email</td>
                <td>Name</td>
                <td>State</td>
                <td>Actions</td>
                <td></td>
            </tr>
            </thead>

            <tbody>
                <tr v-for="subscriber in subscribers" :key="subscriber.id">
                    <td>{{ subscriber.id }}</td>
                    <td>{{ subscriber.email }}</td>
                    <td>{{ subscriber.name }}</td>
                    <td>{{ subscriber.state }}</td>
                    <td><router-link :to="{name: 'Edit', params: { id: subscriber.id }}" class="btn btn-primary">Edit</router-link></td>
                    <td><button class="btn btn-danger"  v-on:click="deleteSubscriber(subscriber.id)">Delete</button></td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>

    export default {
        data(){
            return{
                subscribers: []
            }
        },

        created: function()
        {
            this.fetchSubscribers();
        },

        methods: {
            fetchSubscribers()
            {
              let uri = 'http://localhost/api/subscriber/read.php';
              this.axios.post(uri).then((response) => {
                  this.subscribers = response.data;
              });
            },
            deleteSubscriber(subscriberid)
            {
                let data = {"id": subscriberid}
                let uri = 'http://localhost/api/subscriber/delete.php';
                this.axios.post(uri, data);
            }
        }
    }
</script>